* {
  font-family: sans-serif;
  /* outline: 2px solid black; */
}
body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: hsl(210 30% 24%);
}
.contentlist {
  display: grid;
  place-items: center;
  width: 100%;
}
.App {
  display: grid;
  place-items: center;
  width: 100%;
}
.search {
  width: 60vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.searchbtn {
  display: flex;
  justify-content: center;
}
.textbox {
  padding-left: 10px;
  font-size: 20px;
  width: 50vw;
  height: 40px;
  border: none;
  border-radius: 5px;
  outline: none;
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

.btn {
  cursor: pointer;
  width: 200px;
  height: 40px;
  background-color: #356e32;
  color: white;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border-radius: 10px;
  transition: all 0.3s ease-in;
  font-size: 15px;
}
.btn:hover {
  background-color: #086d03;
}
.add {
  cursor: pointer;
  width: 90px;
  height: 40px;
  background-color: #356e32;
  color: white;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  transition: all 0.3s ease-in;
  font-size: 15px;
}
.add:hover {
  background-color: #086d03;
}

h1 {
  font-size: clamp(3rem, 9vmin, 8rem);
  font-family: sans-serif;
  color: hsl(0 0% 98%);
}

.underlined {
  position: relative;
}

.underline-mask:after {
  content: "";
  position: absolute;
  top: 95%;
  width: 150%;
  aspect-ratio: 3 / 1;
  left: 50%;
  transform: translate(-50%, 0);
  border-radius: 50%;
  border: 6px solid hsl(280 80% 50%);
  /* Use a conic gradient mask to hide and show the bits you want */
  --spread: 140deg;
  --start: 290deg;
  mask: conic-gradient(
    from var(--start),
    white 0 var(--spread),
    transparent var(--spread)
  );
}

.underline-overflow {
  display: inline-block;
  /* A little extra padding and overflow hidden   */
  overflow: hidden;
  padding-bottom: clamp(1rem, 2vmin, 2rem);
}
.underline-overflow:after {
  content: "";
  position: absolute;
  top: 100%;
  height: 150%;
  aspect-ratio: 2.5 / 1;
  left: 50%;
  transform: translate(-50%, -10%);
  border-radius: 50%;
  border: 6px solid hsl(10 80% 50%);
}

.underline-clip:after {
  content: "";
  position: absolute;
  top: 95%;
  width: 150%;
  aspect-ratio: 3 / 1;
  left: 50%;
  transform: translate(-50%, 0);
  border-radius: 50%;
  border: 6px solid hsl(130 80% 50%);
  /* Use a clip-path to hide and show the bits you want */
  clip-path: polygon(0 0, 50% 50%, 100% 0);
}
.list {
  width: 50%;
}
.list ul {
  list-style-type: none;
}
.list ul li {
  text-decoration: none;
  display: grid;
  place-items: center;
  width: 100%;
  height: 50px;
  background-color: #08494e;
  color: white;
  margin-bottom: 20px;
  font-family: sans-serif;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.data {
  width: 360px;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
  background-color: white;
  border-radius: 40px;
  padding: 50px;
}
.data h3 {
  text-align: center;
}
#img {
  width: 200px;
  height: 200px;
}

.ingredients {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 30px;
}
select {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
}

/* Style for the select options */
select option {
  padding: 8px;
  font-size: 16px;
}

/* Style for the default option */
select option[value=""] {
  color: #999;
}

/* Style for the selected option */
select option:checked {
  background-color: #007bff;
  color: #fff;
}
.custom-loader {
  width: 80px;
  height: 52px;
  --c: linear-gradient(#f4f4f4 0 0);
  background: var(--c) 0 100%, var(--c) 50% 100%, var(--c) 100% 100%;
  background-size: 16px calc(100% - 8px);
  background-repeat: no-repeat;
  position: relative;
}
.custom-loader:before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #f4f4f4;
  left: 0;
  top: 0;
  animation: db3-1 3s linear infinite alternate,
    db3-2 1.5s cubic-bezier(0, 200, 0.8, 200) infinite;
}

@keyframes db3-1 {
  100% {
    left: calc(100% - 16px);
  }
}

@keyframes db3-2 {
  100% {
    top: -0.2px;
  }
}

/* CustomAlertBox.css */
.background {
  width: 98vw;
  height: 100vh;

  position: absolute;
}
.custom-alert-box {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  border-radius: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 9999;
  width: 50vw;
  height: 50vh;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.custom-alert-box h1 {
  color: black;
  text-align: center;
}

.custom-alert-box button {
  margin-top: 10px;
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.custom-alert-box button:focus {
  outline: none;
}
#id {
  width: 200px;
  height: 200px;
}
